#root {
  min-height:100vh;
  display:flex;
  flex-direction:column;
  background-color: violet;
}
.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  display: flex;
  align-items: center;
  height: 65px;
  background: linear-gradient(90deg, #672280 1.18%, #a626d3 100%);
  color: white;
  padding: 20px 200px;
}

.header--img {
  height: 100%;
  margin-right: 6px;
}

.header--title {
  font-size: 1.25rem;
  margin-right: auto;
}
.header--project {
  font-size: .75rem;
  font-weight: 500;
}

.form {
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap: 17px;
  max-width: 700px;
  margin: auto;
}
.form > input {
  border-radius: 5px;
  border: 1px solid #d5d4d8;
  text-indent: 5px;
}

.form > button {
  grid-column: 1 / -1;
  background: linear-gradient(90deg, #672280 1.18%, #a626d3 100%);
  color: white;
  border: none;
  cursor: pointer;
}

main {
  padding: 36px;
  max-height: calc(100vh - 65px);
}

.meme > img {
  margin: auto;
  display: block;
  background-color: #282c34;
  max-width: 100%;
  max-height: 400px;
}

.meme {
  position: relative;
  color: white;
  text-align: center;
  max-width: 700px;
  margin: auto;
  min-height: 400px;
  margin-top: 20px;
}

.meme > h2 {
  position: absolute;
  text-shadow: 2px 2px 2px #282c34;
  font-weight: 900;
}

.top {
  top: .5rem;
  width: 100%;
  text-align: center;
}

.bottom {
  bottom: .5rem;
  width: 100%;
  text-align: center;
}

@media (max-width: 700px) {
  header.header {
    padding: 20px;
  }
}